@import "/src/common/styles/sass/variables";

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid white;
    border-radius: 50px;
    width: 100px;
    height: 40px;
    background: black;
    opacity: 0.7;

    &:hover {
        opacity: 0.8;
        cursor: pointer;
        border: 1px solid gold;
    }

    &:hover a {
        color: $textColor;
    }

    a {
        color: white;
        text-decoration: none;
    }
}
